.partnership-map {
  font-size: 1.4rem;
  margin-top: 0 !important;
}

.partnership-map__heading {
  font: 2.4rem/1.4 $font-family--serif;
}

.partnership-map__legend {
  @include reset-list;

  img {
    align-self: center;
    height: auto;
    margin-right: 0.5rem;
    width: 1.7rem;
  }

  li {
    display: flex;
    font-size: 1.3rem;
    font-weight: $font-weight--bold;
    margin: 0;

    + li {
      margin-top: 0.5rem;
    }
  }
}

.partnership-map__sidebar {
  @include respond-to('medium-large') {
    display: flex;
    flex-direction: column;
  }
}

.partnership-map__sidebar__content {
  @include null-margins;

  @include respond-to('medium-large') {
    flex-grow: 1;
  }
}

.partnership-map__sidebar__fineprint {
  font-size: 1.2rem;
  line-height: 1.4;
  margin: 2rem 0 0;

  @include respond-to('medium-large') {
    margin-top: 0;
    transform: translateY(1rem);
  }
}
